import $http from "../../common/api/request.js"
export default{
	state:{
		list:[],
		selectedList:[]
	},
	getters:{
		// 
		checkedAll(state){
			return state.list.length === state.selectedList.length
		},
		
		//
		totalCount(state){
			let total = {
				pprice : 0,
				num : 0 
			}
			state.list.forEach(v=>{
				//
				if(state.selectedList.indexOf(v.id) > -1){
					//
					total.pprice += v.pprice * v.num
					//
					total.num = state.selectedList.length
				}
			})
			return total
			
		}
	},
	mutations:{
		//
		initGetData(state,list){
			state.list = list
		},
		
		//
		checkAll(state){
			state.selectedList = state.list.map(v => {
				v.checked = true
				return v.id
			})
		},
		
		//
		uncheckAll(state){
			state.list.forEach(v => {
				v.checked = false
			})
			
			state.selectedList = []
		},
		
		//
		selectedItem(state,index){
			let id = state.list[index].id
			let i = state.selectedList.indexOf(id)
			//selectedList checked=false  selectedList
			if(i > -1){
				state.list[index].checked = false
				return state.selectedList.splice(i,1)
			}
			// checked = true idselectedList
			state.list[index].checked =true
			state.selectedList.push(id)
		},
		// 
		delGoods(state){
			state.list = state.list.filter(v=>{
				return state.selectedList.indexOf(v.id) === -1
			})
		},
		//
		addShopCart(state,goods){
			state.list.push(goods)
		}
	},
	actions:{
		//
		checkedAllFn({commit,getters}){
			getters.checkedAll ?  commit("uncheckAll")  :  commit("checkAll")
		},
		// 
		delGoodsFn({commit,state}){
			
			uni.showModal({
				content:"",
				success: (res) => {
					$http.request({
						url:"/deleteCart",
						method:"POST",
						header:{
							token:true
						},
						data:{
							goodsId:state.selectedList
						}
					}).then(res => {
						commit('delGoods')//
						commit('uncheckAll')//
						uni.showToast({
							title:"",
							icon:"none"
						})
					})
					
				}
			})
			
			
		}
	}
}