export default{
	state:{
		list:[
			// {
			// 	name:"",
			// 	tel:18057242272,
			// 	city:"",
			// 	details:"",
			// 	isDefault:false
			// },
			// {
			// 	name:"",
			// 	tel:18057242272,
			// 	city:"",
			// 	details:"",
			// 	isDefault:true
			// }
		]
	},
	getters:{
		defaultPath(state){
			
			return state.list.filter(v => {
				return v.isDefault == 1 
			})
		}
	},
	mutations:{
		//
		__initAddressList(state,list){
			// state.list === list
			state.list = list
		},
		
		createPath(state,obj){
			state.list.push(obj)
		},
		
		//
		updatePath(state,{index,item}){
			
			for(let key in item){
				state.list[index] [key] = item[key]
			}
		},
		
		//
		removePath(state){
			state.list.forEach(v=>{
				if(v.isDefault){
					v.isDefault = 0
				}
			})
		}
	},
	actions:{
		createPathFn({commit},obj){
			if(obj.isDefault){
				commit("removePath")
			}
			commit('createPath',obj)
		},
		
		updatePathFn({commit},obj){
			if(obj.item.isDefault){
				commit("removePath")
			}
			commit("updatePath",obj)
		}
	}
}